import {inject} from 'aurelia-framework';
import {Api} from './services/api';

@inject(Api)
export class Post {
	private api: Api;
	
    content = {};
	comments = [];

	constructor(api){
		this.api = api;
	}

	activate(params, routeConfig) {
		this.api.single('post', params.id)
			.then(post => {
				this.content = post;

				routeConfig.navModel.setTitle(post.title.rendered);
			});
	}
}
